@import '_variables';

.clearfix{
	zoom:1;
	&:before, &:after{ content:""; display:table; }
	&:after{ clear: both; }
}



// Bootstrap UI (required)

.nav, .pagination, .carousel, .panel-title a {
		cursor: pointer;
}



// spacing and page layout mods for the design system pages
section.row {
	margin-bottom: 60px;
}
h3.section-head, h3.sh {
	margin        : 50px 0 20px;
	padding-bottom: 5px;
	border-bottom : 1px solid @gray-light;
	clear         : both;
	&:first-child {
		margin-top: 0;
	}
}
h6.sh {
	clear: both;
	margin-top: 40px;
	&:first-child {
		margin-top: 0;
	}
}
hr {
	margin-top: 30px;
	margin-bottom: 30px;
}
.grid-sample .panel {
	border       : none;
	border-top   : 3px solid @gray;
	border-radius: 0;
}


/* HEADER & FOOTER EXAMPLES */
.header-title {
	margin-top: 40px;
	margin-bottom: 20px;
}
.footer-title {
	margin-top: 60px;
	margin-bottom: 20px;
}
.header-example {
	box-shadow: 0 0px 20px rgba(0, 0, 0, 0.1);
	margin-bottom: 20px;
}
.footer-example {
	box-shadow: 0 0px 6px rgba(0, 0, 0, 0.2);

	&.footer-last {
		margin-bottom: 120px;
	}
}

/* Liner Gradients & Pseudo Element */
.pseudo {
  position: relative;
  box-sizing: border-box;
  background-clip: padding-box;
  border-bottom: 10px solid transparent;
  z-index: 2;
  -webkit-filter: drop-shadow(rgba(0, 0, 0, 0.55) 0px 1px 2px);
  background-color: #fff;
}
.pseudo:after {
  content: '';
  position: absolute;
  display: block;
  width: 100%;
  height: 10px;
  bottom: -10px;
  left: 0;
  background-image: linear-gradient(45deg, rgba(0, 0, 0, 0) 33.333%, #ffffff 33.333%, #ffffff 66.667%, rgba(0, 0, 0, 0) 66.667%), linear-gradient(-45deg, rgba(0, 0, 0, 0) 33.333%, #ffffff 33.333%, #ffffff 66.667%, rgba(0, 0, 0, 0) 66.667%);
  background-size: 20px 40px;
  background-position: 50% -30px;
  background-repeat: repeat-x;
  z-index: 1;
}



/* Charting Demos */

.piechart {
	position: relative;
	display: inline-block;

	span {
		font-size  : 20px;
		position   : absolute;
		top        : 0;
		left       : 0;
		display    : block;
		text-align : center;
		width      : 100%;
		height     : 100%;
		padding-top: 35%;
	}
}

.sparkline {
		.chart {
				display: inline-block;
				float: left;
		}
		.caption {
			margin-left: 10px;
			float: left;
			display: inline-block;
		}

		h6 {
				margin-top: 0;
				margin-bottom: 0;
		}

		h2 {
				margin-top: 0;
				margin-bottom: 0;
		}
}


