/* the tab that pops out of the  Live Preview */

@tab-color: #333;

.settings {
		position: fixed;
		top: 60px;
		right: -150px;
		width: 185px;
		height: 100px;
		z-index: 2000;
		transition: all .3s;

		&.activate {
			width: 185px;
			right: 0px;
		}

		.settings-tab {
			float                    : left;
			width                    : 35px;
			text                     : center;
			color                    : #fff;
			background               : @tab-color;
			padding                  : 5px;
			border-top-left-radius   : 5px;
			border-bottom-left-radius: 5px;
			box-shadow               : 3px 5px 8px rgba(0,0,0,.3);
			&:hover {
				cursor: pointer;
			}
			&:active {
				background: #71A6B9;
			}
		}

		.settings-form {
			float                    : right;
			background-color         : @tab-color;
			padding                  : 0px 10px 10px 15px;
			height                   : 125px;
			width                    : 150px;
			color                    : #fff;
			border-bottom-left-radius: 5px;
			box-shadow               : 3px 5px 8px rgba(0,0,0,.3);
			label {
				font-size: 12px !important;
				color: #fff;
			}
		}
}



p[class^="bg"] {
	padding: 10px;
}

/* Color swatches */

.color-swatches {
	margin-bottom: 20px;
	.color-swatch {
		width       : 100%; // set them to use the BS grid
		height      : 72px;
		display: block;
		margin-bottom: 5px;
		&[class*="alert"] {
			 border-right-width: 16px;
			 border-right-style: solid;
		}
	}
	p {
		font-size: 14px;
		color    : @gray-text;
		small {
			font-size: 11px;
			line-height: 1.2;
		}
	}
}


/* color charts */
.color-chart-nested {
	position: relative;
	float: left;
	margin-right: 30px;

	.pie {
		position: absolute;
		top: 86px;
		left: 86px;
	}
}
.color-blocks {

	li {
		margin-bottom: 7px;

		span {
			display: inline-block;
			width: 35px;
			height: 35px;
			vertical-align: middle;
			margin-right: 15px;
		}
	}
}
.donut-varied-padded {
	margin-left: 25px;
	margin-right: 25px;
}


.glyphicons {
	li {
		text-align: center;
		.glyphicon, .pbfont {
			font-size: 2em;
		}
	}
}


